.app {
  margin: 0;
  padding: 0;
  height: 100%;
}
.headerbackground {
  width: 100%;
  height: 150px;
  background-color: steelblue;
  top: 0;
  position: relative;
  margin: 0;
  padding: 0;
}
.header {
  width: 70%;
  float: left;
  margin: 10px;
  font-size: 300%;
}
.info {
  width: 70%;
  float: left;
  margin: 10px;
  font-size: 130%;
}
.buttons {
  position: absolute;
  top: 30px;
  right: 20px;
  cursor: pointer;
}
.logregforms {
  position: absolute;
  top: 200px;
  right: 10px;
}
.newbikeform {
  margin: 0 auto;
  padding: 0;
  
}
.filterbikes {
  position: absolute;
  top: 200px;
  right: 50px;
  margin: 0;
  
}
.footer {
  height: 40px;
  width: 100%;
  position: fixed;
  bottom: 0;
  margin: 40px 0 0 0;
  background-color: steelblue;
}
.footertext {
  margin: 10px 0;
  font-style: italic;
  text-align: center;
}

@media screen and (max-width: 950px) {
  .filterbikes {
    position: absolute;
    top: 150px;
    left: 20px;
    margin: 0;
  }
  .info {
    display: none;
  }
  .bikeslist {
    top: 300px;
  }
  .buttons {
    top: 160px;
  }
  .footer {
    width: 100%;
  }
  .header {
    font-size: 200%;
  }
}